import { Component } from 'react';

import { Lib } from "vendor/utils";

export class Controller extends Component {

	constructor(props, view){
		super(props);
		this._view = view;
		this._tempState = {}
	}

	set view (value) {}
	get view () { 
		return new this._view(this)
	}


	mergeState(state, callback){
		this._tempState = Lib.merge(this._tempState, state);
		this.setState(Lib.merge(this.state, this._tempState), callback);
	}

	render(){
		return (this.view) ? this.view.render() : null;
	}
	
}
