import * as md5 from 'md5';

export class Model {

	constructor(data={}){
		if(data.id){
			this.id = data.id;
		}else{
			this.id = this.uniqueID();
		}

		this._setModel(data);
	}

	uniqueID(){
	  function chr4(){
	    return Math.random().toString(16).slice(-4);
	  }

	  return chr4() + chr4() +
	    '-' + chr4() +
	    '-' + chr4() +
	    '-' + chr4() +
	    '-' + chr4() + chr4() + chr4();
	}

	_setModel(data={}){
		let __model = this._model();

		//SET BASE MODEL
		for(let key in __model){
			let value = __model[key]
			this[key] = value;
		}

		//SET INPUT DATA
		for(let key in __model){
			let value = data[key];
			//let defaultValue = __model[key];

			if(value !== undefined){
				this[key] = data[key];
			} 
		}
	}

	_uniqueKey(){
		return ['id'];
	}

	_resetModel(){
		this._setModel(this._model);
	}


	_model(){
		return {};
	}

	_validation(object, Type){
		let isInstanceof = (object instanceof Type);
		if (!isInstanceof) throw new TypeError("Value of argument 'Type' violates.");
	}

	_cast(object, Type){
		let isInstanceof = (object instanceof Type);

		if(!isInstanceof) return new Type(object);

		return object;
	}

	_getModel(){
		let toExport = {};
		for(let key in this){
			toExport[key] = this[key];
		}

		return toExport;
	}

	getById(_id){
		let search = this.getAll().filter((item, itemId)=>{
			return item.id === _id;
		})

		if(search.length === 1){
			return search[0];
		}

		return null;
	}

	get hash(){
		let keys = this._uniqueKey();
		let uniqueKey = '';

		keys.forEach((key)=>{
			uniqueKey += this[key];
		})
		let hash = md5(uniqueKey.toLowerCase());

		return hash;
	}

	debug(){
		console.log('++++++++++++++++++++++++');
		console.log(...arguments);
		console.log('+++++++++++++++++++++++++');
	}
}

export class ModelCollection extends Model {

	constructor(data){
		super();

		this.collection = {};

		this.addAll(data);
	}

	_childType(){ return Model }

	getUniqueId(data){
		let dataInstance = this._cast(data, this._childType());

		let results = this.getAll().filter((element)=>{
			return !!dataInstance.hash && !!element.hash && dataInstance.hash === element.hash;
		});

		if(results.length > 0){
			return results[0].id;
		}

		return null;
	}

	add(data){
		let dataInstance = this._cast(data, this._childType());

		let uniqueId = this.getUniqueId(dataInstance) || dataInstance.id;

		if(uniqueId) dataInstance.id = uniqueId;

		this.collection[uniqueId] = dataInstance;
	}

	clean(){
		this.collection = {};
	}

	get(index){
		return this.getAll()[index];
	}

	getByName(name){
		let searchResults = this.getAll().filter((element)=>{
			return element.name === name;
		})

		if(searchResults.length === 1) return searchResults[0];

		return null;
	}

	getAll(options){
		if(options){
			if(options.filter){
				return this._getAll(this.collection).filter(options.filter);
			}
			if(options.sort){
				return this._getAll(this.collection).sort(options.sort);
			}
		}

		return this._getAll(this.collection);	
	}

	forEach(forFunction){
		for(let collectionId in this.collection){
			forFunction(this.collection[collectionId]);
		}
	}

	remove(data){
		let dataInstance = this._cast(data, this._childType());

		let uniqueId = this.getUniqueId(dataInstance) || dataInstance.id;

		if(uniqueId) dataInstance.id = uniqueId;

		if(this.collection[dataInstance.id]){
			delete this.collection[dataInstance.id];
		}
	}

	setAndClean(data){
		let dataInstance = this._cast(data, this._childType());

		this.collection = {
			[dataInstance.id]: dataInstance
		}
	}

	count(){
		return Object.keys(this.collection).length;
	}

	addAll(arrayData = []){
		for(let arrayId in arrayData){
			this.add(arrayData[arrayId]);
		}
	}

	_getAll(target){
		let returnList = [];

		for(let targetId in target){
			returnList.push(target[targetId]);
		}

		return returnList;
	}

	static instanceCollection(arrayData = []){
		let instance = new this();


		instance.addAll(arrayData);

		return instance;
	}
}