import { Lib } from 'vendor/utils';

export class OpenPopup{

	constructor(link, options, params){
		this.options = options || {};
		this.params = params || {};

		this.link = link;

		this.onLoad = this.options.onLoad || function () {};
		this.onClose = this.options.onClose || null;

		this.width = this.options.width || 500;
		this.height = this.options.height || 500;
		this.x = (window.screen.width / 2) - (this.width / 2);
		this.y = (window.screen.height / 2) - (this.height / 2);

		this.style = "top=" + this.y + ", left=" + this.x + ", width=" + this.width + ", height=" + this.height + ", status=no, menubar=no, toolbar=no scrollbars=no";

		Lib.set('OpenPopup', this);

	}

	open(){
		this.win = window.open(this.link, "", this.style);

		this.win.onload = function () {
			this.win.onParent = this.params;
			this.onLoad();

		}.bind(this);

		this.win.onbeforeunload = function () {
			if(this.onClose){
				this.onClose();
				return false;
			}
		}.bind(this);
	}

}