import React from 'react';

export class Repeater extends React.Component {

	core() {
		let content = [];

		//content.push(this.props.children.props.children);
		if(this.props.prepend !== null){
			for(let key in  this.props.prepend){
				let value = this.props.prepend[key];
				if(typeof value === "object") value._index = `prepend-${key}`;

				let template = this.props.template(value, `prepend-${key}`)

				content.push(template);
			}
		}

		for(let i in this.props.data){
			if (isNaN(i) && this.props.stringKey === false) continue;

			if(this.props.stringKey && this.contain(i))continue;

			let value = this.props.data[i];
			
			if(typeof value === "object") value._index = i;

			let template = this.props.template(value, i)

			content.push(template);
		}

		if(this.props.append !== null){
			for(let key in  this.props.append){
				let value = this.props.append[key];

				if(typeof value === "object") value._index = `append-${key}`;

				let template = this.props.template(value, `append-${key}`)

				content.push(template);
			}
		}

		return React.createElement(this.props.children.type, this.props.children.props, content);
	}

	contain(keyFind){
		for(let key in this.props.excludeKey){
			let value = this.props.excludeKey[key];

			if (value === keyFind){
				return true;
			}
		}

		return false;
	}

	render(){
		return this.core();	
	}
}