import { Model, Rest } from './';

export class LoopbackInterface extends Model{

	static get METHOD_GET(){ 		return 'GET' 		}; //SELECT
	static get METHOD_POST(){ 		return 'POST' 		}; //INSERT
	static get METHOD_PATCH(){ 		return 'PATCH' 		}; ///UPDATE
	static get METHOD_PUT(){ 		return 'PUT' 		}; //UPSERT
	static get METHOD_DELETE(){ 	return 'DELETE' 	}; //DELETE
	static get METHOD_HEAD(){ 		return 'HEAD' 		}; //HEAD

	constructor(model, path){
		super({ model, path });
	}

	_model(){
		return{
			model: '',
			path: ''
		}
	}

	select(filter = null){
		return this._applyStrategy(
			LoopbackInterface.METHOD_GET,
			'',
			filter
		);
	}

	selectById(id = '', filter = null){
		return this._applyStrategy(
			LoopbackInterface.METHOD_GET,
			`/${id}`,
			filter
		);
	}

	selectFirst(filter){
		return this._applyStrategy(
			LoopbackInterface.METHOD_GET,
			`/findOne`,
			filter
		);
	}

	selectRelated(id, entity){
		return this._applyStrategy(
			LoopbackInterface.METHOD_GET,
			`/${id}/${entity}`,
		);
	}

	existById(id){
		return this._applyStrategy(
			LoopbackInterface.METHOD_HEAD,
			`/${id}`,
			null
		);
	}

	countByFilter(filter){
		return this._applyStrategy(
			LoopbackInterface.METHOD_GET,
			`/count`,
			filter
		);
	}

	insert(data){
		return this._applyStrategy(
			LoopbackInterface.METHOD_POST,
			'',
			null,
			data
		);
	}

	update(data){
		return this._applyStrategy(
			LoopbackInterface.METHOD_PATCH,
			``,
			null,
			data
		);
	}

	updateById(id, data){
		return this._applyStrategy(
			LoopbackInterface.METHOD_PATCH,
			`/${id}`,
			null,
			data
		);
	}

	updateByFilter(filter, data){
		return this._applyStrategy(
			LoopbackInterface.METHOD_POST,
			`/update`,
			filter,
			data
		);
	}

	updateOrCreate(data){
		return this._applyStrategy(
			LoopbackInterface.METHOD_POST,
			`/replaceOrCreate`,
			null,
			data
		);
	}

	replace(data){
		return this._applyStrategy(
			LoopbackInterface.METHOD_PUT,
			``,
			null,
			data
		);
	}

	replaceById(id, data){
		return this._applyStrategy(
			LoopbackInterface.METHOD_PUT,
			`/${id}`,
			null,
			data
		);
	}

	deleteById(id){
		return this._applyStrategy(
			LoopbackInterface.METHOD_DELETE,
			`/${id}`,
			null
		);
	}

	_applyStrategy(){
		let data = this.genereteRequest(...arguments);
		data.headers = data.headers || {};
	
		if (this.token) data.headers.Authorization = this.token;
		return this.constructor.applyStrategy(data, this.path);
	}

	static applyStrategy(data, path){
		data.url = path + data.url;
		return LoopbackInterface.invokeMethod(data);
	}

	static invokeMethod(data){
		return Rest.send(data);
	}

	genereteRequest(method, path, filter, data = null){
		let url = `/${this.model}${path}`;

		if(filter != null) url += `?filter=${JSON.stringify(filter)}`

		let _request = {
			method,
			url,
			data
		}

		return _request;
	}

}